"use client";

export default function MyEventsList({ events = [] }) {
  if (!events.length) {
    return (
      <div className="text-center py-5">
        <img
          src="/images/no-event-image.png"
          alt="No events"
          className="img-fluid mb-3"
          style={{ maxWidth: 300 }}
        />
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">
          Create your first event and unlock the full event-creator experience.
        </p>
      </div>
    );
  }

  return (
    <div className="list-group list-group-flush">
      {events.map((e) => {
        const date = e?.createdAt
          ? new Date(e.createdAt).toLocaleDateString()
          : "";

        return (
          <div
            key={e.id}
            className="list-group-item border-0 mb-3 shadow-sm rounded-4"
          >
            <div className="d-flex align-items-center gap-3">

              {/* IMAGE */}
              <div className="overflow-hidden rounded-3 event-img-sm">
                <img
                  src={
                    e.bannerImage ||
                    "https://img.freepik.com/free-vector/silhouette-party-audience-watercolour-texture_1048-19248.jpg"
                  }
                  className="w-100 h-100"
                  alt={e.title}
                />
              </div>

              {/* INFO */}
              <div className="flex-grow-1 min-w-0">
                <h6 className="fw-semibold text-truncate mb-1">
                  {e.title || "Untitled Event"}
                </h6>
                <small className="text-muted">{date}</small>
              </div>

              {/* ACTIONS */}
              <span
                className={`badge bg-${statusColor(e.status)} me-2`}
              >
                {e.status}
              </span>

              <button className="btn btn-outline-primary btn-sm rounded-pill px-3">
                Edit
              </button>
            </div>
          </div>
        );
      })}
    </div>
  );
}

/* helper */
function statusColor(status) {
  switch (status) {
    case "APPROVED":
      return "primary";
    case "DRAFT":
      return "success";
    case "PENDING":
      return "warning";
    case "REJECTED":
      return "danger";
    case "COMPLETED":
      return "secondary";
    default:
      return "light";
  }
}
