"use client";

export default function MyEventsGrid({ events = [] }) {
  if (!events.length) {
    return (
      <div className="text-center py-5">
        <img
          src="/images/no-event-image.png"
          alt="No events"
          className="img-fluid mb-3"
          style={{ maxWidth: 300 }}
        />
        <h5 className="fw-bold">No events yet</h5>
        <p className="text-muted">
          Create your first event and unlock the full event-creator experience.
        </p>
      </div>
    );
  }

  return (
    <div className="row g-4 p-3 ">
      {events.map((e) => {
        const date = e?.createdAt
          ? new Date(e.createdAt).toLocaleDateString()
          : "";

        return (
          <div key={e.id} className="col-xl-3 col-lg-4 col-md-6 ">
            <div className="card h-100 shadow-sm border-0 rounded-4 shadow-smbg-body-tertiary rounded">

              {/* IMAGE */}
              <div className="position-relative overflow-hidden rounded-top-4 event-img ">
                <img
                  src={
                    e.bannerImage ||
                    "https://img.freepik.com/free-vector/silhouette-party-audience-watercolour-texture_1048-19248.jpg"
                  }
                  className="w-100 h-100"
                  alt={e.title}
                />

                <span
                  className={`badge position-absolute top-0 end-0 m-2 bg-${statusColor(
                    e.status
                  )}`}
                >
                  {e.status}
                </span>
              </div>

              {/* BODY */}
              <div className="card-body d-flex flex-column">
                <h6
                  className="fw-semibold text-truncate"
                  title={e.title}
                >
                  {e.title || "Untitled Event"}
                </h6>

                <small className="text-muted mb-3">
                  Date : {date}
                </small>

                <div className="mt-auto d-flex justify-content-between">
                  <button className="btn btn-outline-primary btn-sm rounded-pill px-3">
                    Edit
                  </button>

                  <button className="btn btn-outline-danger btn-sm rounded-pill px-3">
                    Delete
                  </button>
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

/* helper */
function statusColor(status) {
  switch (status) {
    case "APPROVED":
      return "primary";
    case "DRAFT":
      return "success";
    case "PENDING":
      return "warning";
    case "REJECTED":
      return "danger";
    case "COMPLETED":
      return "secondary";
    default:
      return "light";
  }
}
